import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
import AppTabs from "@/components/app-tabs.vue";
import h5Copy from '@/js_sdk/junyi-h5-copy/junyi-h5-copy/junyi-h5-copy.js'

export default {
	mixins: [MescrollMixin], // 使用mixin (在main.js注册全局组件，内部已注册onPullDownRefresh)
	components: {
		AppTabs
	},
	data() {
		return {
			downOption: {
				native: true // 必须配置此项，且需在pages.json配置"enablePullDownRefresh" : true
			},
			upOption: {
				noMoreSize: 4,
				empty: {
					tip: '~ 暂时没有订单 ~',
					btnText: '去购物'
				},
				page: {
					num: 0,
					size: 20,
					time: null
				},
				textNoMore: "~别再拉了，我是有底线的~"
			},
			goods: [], //列表数据
			tabs: ['全部', '待发货', '配货中', '撤单中', '已发货', "已关闭"],
			status: {
				0: "待发货",
				1: "配货中",
				2: "申请退款",
				3: "已关闭",
				4: "已完成",
				5: "系统关闭"
			},
			tabCodes: ['11', '0', '1', '2', '4', "3"],
			tabIndex: 0, // tab下标,
			tabItem: [],
			tabIndexSel: 0,
		};
	},
	mounted: function() {
		let i = parseInt(this.tabIndexSel);
		this.$refs.appTab.tabClick(i);
	},
	onLoad(options) {
		let state = options.state;
		if (state) {
			this.tabIndexSel = state;
		}
	},

	methods: {
		navTo(url) {
			// if (!this.hasLogin) {
			// 	url = '/pages/public/login';
			// }
			uni.navigateTo({
				url
			})
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item, index) {
			var that = this
			uni.showModal({
				title: '温馨提示',
				content: '确定需要申请撤单么？',
				confirmText: "确定",
				cancelText: "取消",
				success: res2 => {
					if (res2.confirm) {
						const reqData = {
							orderId: item.oid
						}
						this.$http.post(this.$httpApi.order.cancelOrder, reqData).
						then(function(response) {
							that.$api.msg("取消订单成功")
							that.tabItem[index].orderStatus = 3

						})
					}
				}
			});
		},
		upCallback(page) {
			let type = this.tabCodes[this.tabIndex];
			var that = this;
			let orderReq = {
				"type": type,
				"pageNum": page.num,
				"pageSize": page.size
			}
			this.$http.get(this.$httpApi.order.showOrder, orderReq).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.mescroll.endByPage(response.pageSize, response.totalPage);
				if (page.num == 1) that.tabItem = []; //如果是第一页需手动制空列表
				that.tabItem = that.tabItem.concat(response.list); //追加新数据
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
				this.mescroll.endErr();
			});
			// apiSearch(page.num, page.size, keyword).then(curPageData=>{
			// 	//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
			// 	this.mescroll.endSuccess(curPageData.length);
			// 	//设置列表数据
			// 	if(page.num == 1) this.goods = []; //如果是第一页需手动制空列表
			// 	this.goods=this.goods.concat(curPageData); //追加新数据
			// }).catch(()=>{
			// 	//联网失败, 结束加载
			// 	this.mescroll.endErr();
			// })
		},
		//点击空布局按钮的回调
		emptyClick() {
			uni.showToast({
				title: '点击了按钮,具体逻辑自行实现'
			})
		},

		// 切换菜单
		tabChange() {
			this.goods = [] // 先置空列表,显示加载进度
			this.mescroll.resetUpScroll() // 再刷新列表数据
		},
		//点击复制订单号
		copyOrderNo(orderNo) {
			let content = orderNo // 复制内容，必须字符串，数字需要转换为字符串
			const result = h5Copy(content)
			if (result === false) {
				uni.showToast({
					title: '不支持',
				})
			} else {
				uni.showToast({
					title: '单号复制成功',
					icon: 'none'
				})
			}
		}

	},
}
